.body {
  background: $color-bg;
  color: $color-text;
  font-family: $font-family;
  font-size: $font-size;
  letter-spacing: $letter-spacing;
  line-height: $line-height;
  scroll-behavior: smooth;

  &.--frozen {
      overflow: hidden;
  }
}

.container {
  max-width: $container-width;
  padding-left: $container-padding-left; 
  padding-right: $container-padding-right;
  margin: 0 auto;
  position: relative;
  z-index: 50;
  width: 100%;
}

.hidden {
  display: none;
}

.content{
  position: relative;
  z-index: 10;
}

a {
  cursor: pointer;
  color: $link-color;
  text-decoration: none;
}


strong {
  font-weight: bold;
}

::placeholder {
  color: $placeholder;
  transition: .4s;
}

:focus::placeholder {
  opacity: 0;
}

::-webkit-scrollbar {
  width: 0px; 
  background: transparent;
}

img{
  max-width: 100%;
  display: block;
}

form{
  display: block;
  width: 100%;
}

.link{
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  text-decoration: underline;
  &.--green a{
    color: #48F894;
  }
}



h1, h2, h3, h4, h5, h6{    
  font-weight: normal;
}

.h2{
  font-weight: 600;
  font-size: 34px;
  line-height: 30px;
  color: #005BDE;
  margin-bottom: 45px;
  text-transform: uppercase;
  &.--center{
    text-align: center;
  }
  &.--white{
    color: #fff;
  }
}

@media(max-width: 980px){
  .h2{
    font-size: 22px;
    line-height: 30px;
    margin-bottom: 15px;
  }
}

