.main{
  padding-top: 146px;
  background: url(../img/main.png) no-repeat center / cover;
  padding-bottom: 90px;
}

.main__wrap{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .btn-center{
    width: 100%;
  }
}

.main__content{
  width: calc(100% - 465px);
  text-align: right;
  .btn-center {
    display: none;
  }
  .link{
    margin-bottom: 42px;
  }
}

.main__logo{
  transform: translateY(-110px);
}

.main__text{
  color: #fff;
  display: flex;
  justify-content: flex-end;
  padding-top: 60px;
  text-align: left;
  p{
    max-width: 730px;
  }
}

.main__btn{
  max-width: 334px;
  display: inline-block;
  text-align: center;
}

.about {
  padding-top: 74px;
  padding-bottom: 45px;
  z-index: 100;
  position: relative;
  &:before{
    content: "";
    position: absolute;
    right: 0;
    width: 50%;
    top: 0;
    height: 100%;
    background-color: #fff;
    z-index: 10;
  }
  &:after{
    content: "";
    position: absolute;
    width: 1158px;
    height: 661px;
    left: 50%;
    transform: translateX(-50%);
    margin-left: -270px;
    bottom: -212px;
    background: url(../img/about__bg.svg) no-repeat center / contain;
  }
}

.about__text{
  display: flex;
  justify-content: flex-end;
  position: relative;
  z-index: 20;
  p{
    width: 50%;
    font-size: 20px;
    line-height: 30px;
    padding-left: 32px;
  }
}

.timer{
  background: url(../img/timer__bg.jpg) no-repeat center / cover;
  padding: 90px 0 74px;
}

.timer__panel{
  background: #FFFFFF;
  border-radius: 16px;
  padding: 35px;
  text-align: center;
  max-width: 872px;
  margin: 0 auto 76px;
  .h2{
    margin-bottom: 15px;
    font-weight: 500;
  }
}

.timer__description{
  margin-bottom: 40px;
  color:  #003B6A;
}

.timer__block{
  display: flex;
  justify-content: center;
}

.timer__number{
  font-weight: 800;
  font-size: 60px;
  line-height: 30px;
  color: #005BDE;
  margin-bottom: 35px;
}

.timer__item--center{
  padding-left: 75px;
  padding-right: 75px;
  .timer__number{
    &:after, 
    &:before{
      content: ":";
      position: absolute;
      top: -5px;
    }
    &:after{
      left: 100%;
      margin-left: 30px;
    }
    &:before{
      right: 100%;
      margin-right: 30px;
    }
  }
}

.timer__text{
  font-weight: 800;
  font-size: 16px;
  line-height: 30px;
  color: #003B6A;
}

.calendar {
  padding-top: 75px;
  padding-bottom: 100px;
}

.caleandar__list{
  display: flex;
  margin-right: -30px;
  margin-bottom: 30px;
  padding-top: 45px;
}


.calendar__item {
  position: relative;
  width: calc(25% - 30px);
  margin-right: 30px;
  
  &:nth-child(1) .calendar__panel{
    background-color: rgba(76, 178, 120, 0.1);
  }
  &:nth-child(2) .calendar__panel{
    background-color: rgba(76, 178, 120, 0.1);
  }
  &:nth-child(3) .calendar__panel{
    background-color: rgba(248, 203, 123, 0.31);
  }
  &:nth-child(4) .calendar__panel{
    background-color: rgba(237, 111, 79, 0.34);
  }
}

.calendar__panel{
  padding-top: 90px;
  text-align: center;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 33px;
  height: 200px;
  border-radius: 16px;
  padding-left: 15px;
  padding-right: 15px;

}

.calendar__number{
  width: 87px;
  height: 87px;
  border-radius: 50%;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 50%;
  top: 0;
  transform: translate(-50%, -50%);
  font-weight: 800;
  font-size: 48px;
  line-height: 30px;
  color: #06C358;
}

.calendar__date{
  font-weight: 600;
  font-size: 32px;
  line-height: 30px;
  color: #003B6A;
  text-align: center;
}

.organizations {
  padding: 46px 0 27px;
  background: url(../img/footer.jpg) no-repeat top center/ cover;
}

.organizations__panels{
  display: flex;
  justify-content: center;
  margin-bottom: 76px;
}

.organizations__panel{
  width: 100%;
  max-width: 320px;
  margin: 0 14px;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 180px;
  border-radius: 16px;
  &:first-child{
    margin-left: 0px;
  }
  &:last-child{
    margin-right: 0px;
  }
}

.footer__wrap{
  display: flex;
  justify-content: space-between;
  color: #fff;
  font-size: 14px;
}

.copyright{
  max-width: 298px;
}

.contacts{
  a{
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 3px;
    display: block;
    color: #fff;
    &:last-child{
      margin-bottom: 0px;
    }
  }
}

.socilas {
  display: flex;
  justify-content: center;
}

.social{
  margin-right: 20px;
  margin-left: 20px;
  &:first-child{
    margin-left: 0px;
  }
  &:last-child{
    margin-right: 0px;
  }
}

.political{
  max-width: 240px;
  a{
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #fff;
    text-decoration: underline;
  }
}

.footer__lk{
  text-transform: uppercase;
}



@media(max-width: 1300px){
  .main__text p{
    max-width: 460px;
  }
} // 1300

@media(max-width: 980px){
  .main{
    padding-top: 21px;
  }
  .main__logo{
    max-width: 320px;
    margin-bottom: 62px;
  }
  .main__wrap{
    .btn-center{
      display: none;
    }
  }
  .main__content{
    text-align: left;
    .btn-center{
      display: flex;
      justify-content: flex-start;
      margin-bottom: 40px;
    }
  }
  .main__wrap .btn-center{
    .btn{
      align-items: center;
      display: inline-flex;
    }
    span{
      display: none;
    }
  }
  .main__content{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }
  .main__content .link{
    font-weight: 600;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 30px;
    width: 100%;
  }
  .main__text{
    width: 100%;
    order: -1;
    display: block;
    padding-top: 0px;
    font-size: 14px;
    margin-bottom: 40px;
  }
  .about:after{
    display: none;
  }
  .about__text p{
    width: 100%;
    font-size: 13px;
    line-height: 20px;
    padding-left: 0px;
  }
  .about{
    padding: 27px 0;
  }
  .timer{
    padding-top: 33px;
    padding-bottom: 39px;
  }
  .timer__panel{
    padding: 0;
    background-color: transparent;
    margin-bottom: 86px;
  }
  .timer__panel .h2{
    color: #fff;
  }
  .timer__description{
    font-size: 16px;
    margin-bottom: 70px;
    font-weight: bold;
    color: #003B6A;
  }
  .timer__block{
    background-color: #fff;
    padding-top: 45px;
    padding-bottom: 30px;
    border-radius: 16px;
    max-width: 450px;
    margin: 0 auto;
  }
  .timer__number{
    font-size: 54px;
    line-height: 30px;
    margin-bottom: 10px;
  }
  .timer__text{
    font-size: 14px;
    line-height: 30px;
  }
  .timer__item--center{
    padding-left: 45px;
    padding-right: 45px;
  }
  .timer__item--center .timer__number:after{
    margin-left: 17px;
  }
  .timer__item--center .timer__number:before{
    margin-right: 17px;
  }
  .caleandar__list{
    flex-wrap: wrap;
  }
  .calendar__item{
    width: calc(50% - 30px);
    margin-bottom: 80px;
    padding-bottom: 34px;
    border-radius: 16px;
    &:nth-child(1){
      background-color: rgba(76, 178, 120, 0.1);
    }
    &:nth-child(2){
      background-color: rgba(76, 178, 120, 0.1);
    }
    &:nth-child(3){
      background-color: rgba(248, 203, 123, 0.31);
    }
    &:nth-child(4){
      background-color: rgba(237, 111, 79, 0.34);
    }
    &:nth-child(1) .calendar__panel{
      background-color: transparent;
    }
    &:nth-child(2) .calendar__panel{
      background-color: transparent;
    }
    &:nth-child(3) .calendar__panel{
      background-color: transparent;
    }
    &:nth-child(4) .calendar__panel{
      background-color: transparent;
    }
    .calendar__panel{
      height: auto;
      padding-top: 58px;
    }
  }
  
} // 980


@media(max-width: 768px){
  .calendar__item{
    width: calc(100% - 30px);
  }
  .calendar{
    padding-bottom: 0px;
  }
  .organizations__panels{
    flex-direction: column;
    padding-top: 20px;
  }
  .organizations__panel,
  .organizations__panel:first-child,
  .organizations__panel:last-child{
    margin: 0 auto 20px;
  }
}

@media(max-width: 530px){
  .main__wrap .btn-center .btn{
    width: 100%;
    max-width: 100%;
  }
  .main__content .link{
    text-align: center;
  }
  .main{
    padding-bottom: 20px;
  }
} // 530

@media(max-width: 1250px){
  .footer__wrap {
    flex-wrap: wrap;
  }
  .copyright{
    order: 1;
    width: 50%;
    padding-top: 30px;
    max-width: 100%;
  }
  .political{
    order: 1;
    width: 50%;
    text-align: right;
    padding-top: 26px;
  }
}

@media(max-width: 768px){
  .footer__lk{
    width: 100%;
    order: -1;
    text-align: center;
    margin-bottom: 49px;
    a{
      font-size: 16px;
      line-height: 20px;
    }
  }
  .contacts{
    width: 100%;
    text-align: center;
    margin-bottom: 60px;
  }
  .socilas {
    width: 100%;
    margin-bottom: 50px;
  }
  .social{
    width: 40px;
    height: 40px;
  }
  .political{
    width: 100%;
    text-align: center;
    padding-top: 0px;
    max-width: 100%;
    order: 0;
    margin-bottom: 50px;
  }
  .copyright{
    padding-top: 0px;
    font-size: 14px;
    line-height: 20px;
    color: #6BA8FF;
    text-align: center;
    width: 100%;
    max-width: 360px;
    margin: 0 auto;
  }
}