.inner{
  padding-top: 90px;
  padding-bottom: 160px;
  min-height: calc(100vh - 120px);
}

.btn-submit{
  padding-top: 30px;
}

.input__name{
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  &.req:after{
    content: "*";
    color: #ED6F4F;
  }
}

.form__white,
.form__green{
  padding-left: 155px;
}

.form__green{
  background-color: rgba(76, 178, 120, 0.1);;
  padding-top: 30px;
  padding-bottom: 30px;
  margin-bottom: 30px;
  .form__block:last-child{
    margin-bottom: 0px;
  }
}

.form__block{
  max-width: 742px;
}

.form__block--line{
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  .input__name{
    width: 306px;
    margin-bottom: 0px;
  }
  .input, .select{
    width: calc(100% - 306px);
    margin-bottom: 0px;
    input,
    .input{
      width: 100%;
    }
  }
}

.form__block--sqare{
  .input__name{
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 22px;
  }
}


.input{
  position: relative;
  margin-bottom: 30px;
  &.--error input{
    color: #fff;
    border-color:  #ED6F4F;
  }
  &.--succed input{
    color: #fff;
    border-color:#005BDE ;
  }
  input:focus{
    border:  3px solid #005BDE;
    font-weight: 600;
  }
  input{
    font-size: 20px;
    color: #005BDE;
    padding-left: 20px;
    padding-right: 40px;
    width: 100%;
    height: 60px;
    border: 1px solid #005BDE;
    border-radius: 6px;
    background-color: #fff;
    &:focus{
      
    }
  }
}

.error__message{
  margin-top: 10px;
  color:  #ED6F4F;
  display: none;
  .--error  &{
    display: block;
  }
}


.select__main{
  position: relative;
  input{
    cursor: pointer;
  }
  &.active{
    &:after{
      transform: translateY(-50%) rotateZ(180deg);
    }
  }
  &:after{
    content: "";
    position: absolute;
    right: 21px;
    top: 50%;
    transform: translateY(-50%) rotateZ(0);
    width: 17px;
    height: 10px;
    background: url(../img/select.svg) no-repeat center / contain;
    transition: .4s;
  }
}

.select__drop{
  position: absolute;
  width: 100%;
  left: 0;
  top: 100%;
  margin-top: 10px;
  box-shadow: 0px 4px 24px rgba(0, 0, 0, 0.08);
  border-radius: 6px;
  background-color: #fff;
  z-index: 200;
  display: none;
}

.select__item{
  padding: 5px 10px;
  &:hover{
    background-color: #005BDE;
    color: #fff;
  }
}


.footer{
  background: url(../img/footer.jpg) no-repeat top center/ cover;
  padding-top: 47px;
  padding-bottom: 20px;
}

.inner__cont{
  display: flex;
  flex-wrap: wrap;
  padding-top: 0px;
  padding-bottom: 0px;
  background: url(../img/form-bg.jpg);
}

.inner__cont-top{
  width: 100%;
  padding-bottom: 60px;
}

.inner__cont-bottom{
  width: 100%;
  align-self: flex-end;
}

.inner__cont-bottom.no-footer{
  .footer__wrap {
    padding-bottom: 27px;
    padding-top: 27px;
  }
}

.form__panel{
  background-color: #fff;
  padding: 74px 70px;
  max-width: 500px;
  margin:50px auto 0;
  border-radius: 14px;
  width: calc(100% - 40px);
  .input{
    max-width: 356px;
    margin: 0 auto 30px;
  }
  &.--password {
    max-width: 668px;
    .btn-center{
      padding-top: 30px;
    }
  }
  &.--recover{
    max-width: 668px;
  }
  .btn.--green{
    width: 100%;
    max-width: 254px;
  }
}

.form__links {
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px;
  a{
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    color: #06C358;
    text-decoration: underline;
  }
}

.form__descriprion{
  margin-top: -22px;
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
}

.form__help{
  font-weight: 600;
  font-size: 15px;
  line-height: 18px;
  color: #06C358;
  text-align: center;
  padding-top: 10px;
  margin-bottom: 40px;
}


@media(max-width: 1400px){
  .header__logo{
    max-width: 250px;
  }
  .header-inner{
    .nav__link{
      font-size: 16px;
      margin-right: 10px;
    }
  }
}

@media(max-width: 1200px){
  .header-inner{
    .nav{
      margin-right: 30px;
    }
    .enter{
      width: 170px;
      font-size: 14px;
    }
  }
}

@media(max-width: 980px){
  .form__white, .form__green{
    padding-left: 0px;
  }
  .form__descriprion{
    margin-top: 0px;
    font-size: 18px;
    line-height: 20px;
  }
  .form__help{
    margin-top: -10px;
    margin-bottom: 20px;
  }
}

@media(max-width: 768px){
  .form__panel.--password .btn-center{
    padding-top: 10px;
  }
  .form__panel{
    padding: 37px 22px 57px;
  }
  .form__block--line{
    display: block;
  }
  .form__block--line .input__name{
    margin-bottom: 15px;
  }
  .input__name {
    margin-bottom: 15px;
    font-size: 16px;
  }
  .form__block--line .input, .form__block--line .select{
    width: 100%;
  }
  .header__logo{
    padding-right: 10px;
  }
  .header-inner{
    height: 90px;
  }
  .header-inner .header__right{
    padding-top: 0px;
  }
}