.header{
  padding-top: 30px;
}

.header-main{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}

.nav{
  font-weight: 800;
  font-size: 20px;
  line-height: 24px;
  margin-right: 80px;
}

.nav__link{
  font-weight: 800;
  font-size: 20px;
  line-height: 24px;
  color: #2CE37B;
  text-transform: uppercase;
  margin-right: 30px;
  &.active{
    color: #fff;
  }
  &:last-child{
    margin-right: 0px;
  }
}

.mobile-menu{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: linear-gradient(180deg, #0047AD 0%, #0060EC 100%);
  padding-top: 120px;
  overflow-y: auto;
  display: none;
  &.active{
    display: block;
  }
  .nav{
    margin-bottom: 60px;
    margin-right: 0px;
  }
  .nav__link{
    width: 100%;
    margin-bottom: 35px;
    font-size: 26px;
    display: block;
    text-align: center;
    margin-right: 0px;
    &.active{
      color: #fff;
    }
  }
  .enter, .btn{
    margin: 0 auto 16px;
    display: flex;
    width: 100%;
    max-width: 360px;
  }
  .burger{
    display: flex;
    position: absolute;
    right: 20px;
    top: 20px;
  }
}

.burger{
  display: none;
  width: 50px;
  height: 50px;
  border-radius: 4px;
  border: 3px solid #fff;
  justify-content: center;
  align-items: center;
}


.header-inner{
  height: 120px;
  background: url(../img/main.png) no-repeat center bottom / cover;
  padding-top: 17px;
  .container{
    flex-wrap: nowrap;
  }
  .header__right{
    flex-wrap: nowrap;
    align-self: flex-start;
    padding-top: 18px;
  }
  .enter{
    width: 200px;
    height: 50px;
    font-size: 17px;
  }
}






@media(max-width: 1200px){
  .main__logo{
    transform: none;
    margin-bottom: 40px;
  }
  .nav__link {
    font-size: 16px;
  }
}


@media(max-width: 980px){
  .header{
    padding-top: 20px;
  }
  .header__right {
    .nav{
      display: none;
    }
    .enter{
      display: none;
    }
    .burger{
      display: flex;
    }
  }
}

